export const sat_data={"20580":{"color":"#EE82EE","type":"Astronomy","launch_date":"April 24, 1990, 12:33:51 UTC","mission_duration":"Elapsed: 30 years, 4 months, 22 days","launch_mass":"11,110 kg (24,490 lb)","isGeoStationary":false,"tle_line1":"1 20580U 90037B   20259.12197917  .00000388  00000-0  12562-4 0  9998","tle_line2":"2 20580  28.4696  86.5709 0002641 340.1610 310.9225 15.09430828469523","name":"Hubble Space Telescope ","sat_name":"HST","country_name":"USA","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/america.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon4.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/hubble_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/hubble_2.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/hubble_3.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/hubble_4.jpg"],"use_cases":["Hubble, the observatory, is the first major optical telescope to be placed in space, the ultimate mountaintop. Above the distortion of the atmosphere, far far above rain clouds and light pollution, Hubble has an unobstructed view of the universe.","Scientists have used Hubble to observe the most distant stars and galaxies as well as the planets in our solar system."," The telescope is able to lock onto a target without deviating more than 7/1000th of an arcsecond, or about the width of a human hair seen at a distance of 1 mile.","Hubble transmits about 150 gigabits of raw science data every week."],"description":"The Hubble Space Telescope (often referred to as HST or Hubble) is a space telescope that was launched into low Earth orbit in 1990 and remains in operation. It was not the first space telescope, but it is one of the largest and most versatile, well known both as a vital research tool and as a public relations boon for astronomy. The Hubble telescope is named after astronomer Edwin Hubble.The Hubble telescope was built by the United States space agency NASA with contributions from the European Space Agency.Hubble is the only telescope designed to be maintained in space by astronauts. Five Space Shuttle missions have repaired, upgraded, and replaced systems on the telescope, including all five of the main instruments. The fifth mission was canceled on safety grounds following the Columbia disaster (2003), but NASA administrator Michael D. Griffin approved the fifth servicing mission which was completed in 2009. The telescope was still operating as of April 24, 2020, its 30th anniversary and could last until 2030–2040.One successor to the Hubble telescope is the James Webb Space Telescope (JWST) which is scheduled to be launched in late 2021."},"25544":{"color":"#FF0000","type":"Scientific research,Exploration,Education and cultural outreach","launch_date":"20 November 1998; 21 years ago","mission_duration":"It has no fixed duration but every six months three to six crew members join there to monitor the space  ","launch_mass":"419,725 kg (925,335 lb)","isGeoStationary":false,"tle_line1":"1 25544U 98067A   20259.60133280  .00000295  00000-0  13495-4 0  9993","tle_line2":"2 25544  51.6439 261.0466 0000952 103.4889 359.3413 15.48949242246035","name":"International Space Station ","sat_name":"ISS (ZARYA)","country_name":"USA","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/america.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon8.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/ISS_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/ISS_2.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/ISS_3.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/ISS_4.jpg"],"use_cases":["Commercializing low-Earth orbit","Growing high-quality protein crystals","Supporting water purification efforts worldwide.","Bringing space station ultrasound to the ends of the Earth","Improving eye surgery with space hardware","Providing students opportunities to conduct their own science in space"],"description":"The International Space Station (ISS) is a modular space station (habitable artificial satellite) in low Earth orbit. It is a multinational collaborative project between five participating space agencies: NASA (United States), Roscosmos (Russia), JAXA (Japan), ESA (Europe), and CSA (Canada).The station is divided into two sections: the Russian Orbital Segment (ROS), operated by Russia; and the United States Orbital Segment (USOS), which is shared by many nations.The ISS was originally intended to be a laboratory, observatory, and factory while providing transportation, maintenance, and a low Earth orbit staging base for possible future missions to the Moon, Mars, and asteroids. However, not all of the uses envisioned in the initial memorandum of understanding between NASA and Roscosmos have come to fruition. In the 2010 United States National Space Policy, the ISS was given additional roles of serving commercial, diplomatic,and educational purposes.The ISS is a third generation modular space station. Modular stations can allow modules to be added to or removed from the existing structure, allowing greater flexibility.Scientific research,monitoring satellite,benifitting earth with satellite are the main targets of ISS."},"25994":{"color":"#BB00FF","type":"Climate research","launch_date":"December 18, 1999, 18:57:39 UTC","mission_duration":"Elapsed: 20 years, 9 months, 4 days","launch_mass":"4,864 kilograms (10,723 lb)","isGeoStationary":false,"tle_line1":"1 25994U 99068A   20266.47514468  .00000052  00000-0  21670-4 0  9993","tle_line2":"2 25994  98.2050 339.3794 0001426 101.0110 259.1263 14.57111020104443","name":"EOS AM-1","sat_name":"TERRA","country_name":"USA","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/america.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon3.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/TERRA_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/TERRA_2.jpg"],"use_cases":["Identifying forest stands and tallying their area to estimate forest supplies (MODIS)","Monitoring air quality in the lower atmosphere (MOPITT)","Fighting wildfires by planning firefighter dispatch (MODIS)"],"description":"Terra (EOS AM-1) is a multi-national NASA scientific research satellite in a Sun-synchronous orbit around the Earth. It is the flagship of the Earth Observing System (EOS). The name \"Terra\" comes from the Latin word for Earth. A naming contest was held by NASA among U.S. high school students. The winning essay was submitted by Sasha Jones of Brentwood, Missouri. The identifier \"AM-1\" refers to its orbit, passing over the equator in the morning.The satellite was launched from Vandenberg Air Force Base on December 18th, 1999, aboard an Atlas IIAS vehicle and began collecting data on February 24th, 2000. It was placed into a near-polar, sun-synchronous orbit at an altitude of 705 km (438 mi), with a 10:30am descending node."},"27424":{"color":"#00FFFF","type":"Earth observation ","launch_date":"May 4, 2002, 09:54:58 UTC","mission_duration":"6 years (planned) Elapsed: 18 years, 4 months, 11 days","launch_mass":"3,117 kilograms (6,872 lb)","isGeoStationary":false,"tle_line1":"1 27424U 02022A   20259.51567562  .00000043  00000-0  19676-4 0  9990","tle_line2":"2 27424  98.2016 198.8497 0002757  72.7828  59.7461 14.57113259977034","name":"Aqua","sat_name":"Aqua","country_name":"USA","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/america.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon4.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Aqua_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Aqua_2.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Aqua_3.jpg"],"use_cases":["Its mission is collecting about the Earth's water cycle, including evaporation from the oceans, water vapor in the atmosphere, clouds, precipitation, soil moisture, sea ice, land ice, and snow cover on the land and ice","Additional variables also being measured by Aqua include radiative energy fluxes, aerosols, vegetation cover on the land, phytoplankton and dissolved organic matter in the oceans, and air, land, and water temperatures."],"description":" Advanced Microwave Scanning Radiometer-EOS (AMSR-E)Aqua (EOS PM-1) is a NASA scientific research satellite in orbit around the Earth, studying the precipitation, evaporation, and cycling of water. It is the second major component of the Earth Observing System (EOS) preceded by Terra (launched 1999) and followed by Aura (launched 2004).The name \"Aqua\" comes from the Latin word for water. The satellite was launched from Vandenberg Air Force Base on May 4, 2002, aboard a Delta II rocket. Aqua is on a Sun-synchronous orbit.Aqua carries six instruments for studies of water on the Earth's surface and in the atmosphere, of which four are still operating:Advanced Microwave Scanning Radiometer-EOS (AMSR-E),Moderate Resolution Imaging Spectroradiometer (MODIS),Advanced Microwave Sounding Unit (AMSU-A),Atmospheric Infrared Sounder (AIRS),Humidity Sounder for Brazil (HSB),Clouds and the Earth's Radiant Energy System (CERES)."},"27704":{"color":"#223322","type":"Navigation","launch_date":"31 March 2003, 22:09:01 UTC","mission_duration":"10 years (planned)","launch_mass":"2,032 kilograms (4,480 lb)","isGeoStationary":false,"tle_line1":"1 27704U 03010A   20259.75326073 -.00000031  00000-0  00000-0 0  9990","tle_line2":"2 27704  54.6899  49.3148 0239879 285.8854  85.1011  2.00404354127985","name":"(NAVSTAR 52)USA-168","sat_name":"GPS BIIR-9  (PRN 21)","country_name":"USA ","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/america.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon2.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/USA-168_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/USA-168_2.jpg"],"use_cases":["It is used to broadcast the PRN 21 signal, and operates in slot 3 of plane D of the GPS constellation","It monitores global position"],"description":"USA-168, also known as GPS IIR-9 and GPS SVN-45, is an American navigation satellite which forms part of the Global Positioning System. It was the ninth Block IIR GPS satellite to be launched, out of thirteen in the original configuration, and twenty one overall. It was built by Lockheed Martin, using the AS-4000 satellite bus.USA-168 was launched at 22:09:01 UTC on 31 March 2003, atop a Delta II carrier rocket, flight number D297, flying in the 7925-9.5 configuration. The launch took place from Space Launch Complex 17A at the Cape Canaveral Air Force Station, and placed USA-168 into a transfer orbit. The satellite raised itself into medium Earth orbit using a Star-37FM apogee motor.By 3 April 2003, USA-168 was in an orbit with a perigee of 20,063 kilometres (12,467 mi), an apogee of 20,433 kilometres (12,696 mi), a period of 720.64 minutes, and 54.9 degrees of inclination to the equator.It is used to broadcast the PRN 21 signal, and operates in slot 3 of plane D of the GPS constellation. The satellite has a mass of 2,032 kilograms (4,480 lb), and a design life of 10 years.As of 2012 it remains in service."},"28051":{"color":"#3D9999","type":"Earth observation,Remote sensing","launch_date":"17 October 2003 at 04:54:00 UTC","mission_duration":"5 years (planned),16 years, 10 months, 29 days (elapsed)","launch_mass":"1360 kg","isGeoStationary":false,"tle_line1":"1 28051U 03046A   20261.44818455 -.00000043  00000-0 -31481-6 0  9991","tle_line2":"2 28051  98.4772 336.3329 0059244 113.5359 247.2064 14.34169551879575","name":"RESOURCESAT-1","sat_name":"IRS-P6 (RESOURCESAT-1)","country_name":"India","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/India.jpg","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon5.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/ResourceSat-1_1.jpeg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/ResourceSat-1_2.jpg"],"use_cases":["The overall objectives of the IRS-P6 mission (ResourceSat-1) are to provide continued remote sensing data services on an operational basis for integrated land and water resources management. IRS-P6 is the continuation of the IRS-1C/1D missions with considerably enhanced capabilities."],"description":"Resourcesat-1 (also known as IRS-P6) is an advanced remote sensing satellite built by Indian Space Research Organization (ISRO). The tenth satellite of ISRO in IRS series, Resourcesat-1 is intended to not only continue the remote sensing data services provided by IRS-1C and IRS-1D, both of which have far outlived their designed mission lives, but also vastly enhance the data quality.The 1360 kg Resourcesat-1 was launched into an 817 km high polar sun synchronous orbit by the eighth flight of India's Polar Satellite Launch Vehicle (PSLV-C5)."},"28649":{"color":"#0AA066","type":"Earth observation","launch_date":"5 May 2005 at 04:44:00 UTC","mission_duration":"5 years (planned),15 years, 4 months, 14 days (elapsed)","launch_mass":"1560 kg","isGeoStationary":false,"tle_line1":"1 28649U 05017A   20263.45007498  .00000020  00000-0  86585-5 0  9991","tle_line2":"2 28649  97.8049 330.3012 0002633 114.0740 246.0750 14.83710410832456","name":"CARTOSAT-1","sat_name":"IRS-P5 (CARTOSAT-1)","country_name":"India","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/India.jpg","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon2.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Cartosat-1_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Cartosat-1_2.jpg"],"use_cases":["It is capable of providing in-orbit stereo images","The satellite will be used for a variety of earth-observation applications, such as cartography, weather mapping, forest surveys, urban planning, coastal studies, mineral prospecting, and, of course, military purposes."],"description":"Department of Space, Government of India, has launched a series of satellites for Earth's resource management and monitoring. These satellites have been very successful in providing data in various scales ranging from 1:1 Million to 1:12,500 scale. Each of the Indian Remote Sensing satellite missions ensured data continuity while introducing improvements in the spatial, spectral and radiometric resolutions. Considering increase demand for large scale and topographic mapping data DOS launched the Cartosat-1 in 2005. Cartosat-1 is dedicated to stereo viewing for large scale mapping and terrain modelling.Cartosat-1 or IRS-P5 is a stereoscopic Earth observation satellite in a sun-synchronous orbit, and the first one of the Cartosat series of satellites. The eleventh satellite of ISRO in IRS series. The satellite was built, launched and maintained by the Indian Space Research Organisation. Weighing around 1560 kg at launch, its applications will mainly be towards cartography in India. It was launched by PSLV-C6, on 5 May 2005 at 04:44:00 UTC from the newly built Second Launch Pad at Sriharikota.  Images from the satellite will be available from GeoEye for worldwide distribution. The satellite covers the entire globe in 1867 orbits on a 126-day cycle. Adjacent paths are covered by a separation of eleven days."},"28893":{"color":"#FF8899","type":"Reconnaissance","launch_date":"27 October 2005, 06:52:26 UTC","mission_duration":"Not defined","launch_mass":"170 kg","isGeoStationary":false,"tle_line1":"1 28893U 05043D   20262.37754426 -.00000022  00000-0  48366-5 0  9999","tle_line2":"2 28893  98.0356  30.0500 0015509  16.4106 343.7595 14.61538331794156","name":"First Iranian artificial satellite","sat_name":"SINAH-1","country_name":"Iran","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Iran.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon2.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Sinah-1_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Sinah-1_2.jpg"],"use_cases":["It serves telecommunications and helps in earth imageing research"],"description":"Sina-1 (Persian: سینا ۱‎) is the first Iranian artificial satellite, launched at 6:52 UTC October 28, 2005 on board a Cosmos-3M Russian launch vehicle from the Plesetsk Cosmodrome. The rocket was also carrying a Russian military Mozhayets-5 satellite, a Chinese China-DMC, a British TopSat, a European Space Agency SSETI Express (Student Space Exploration and Technology Initiative-Express), a Norwegian nCube, a German UWE-1, and a Japanese XI-V. Sina-1 Satellite Catalog Number or USSPACECOM object number is 28893.The Iranian Space Agency had for many years said they were on the verge of sending their first satellite into orbit, finally leading to the launch of Sina-1, a satellite for telecommunications and Earth-imaging research purposes.The miniaturized 170-kilogram reconnaissance satellite was put into a sun-synchronous near-polar orbit and will image the surface with a 50 m (pan) with 50 km swath and 250 m resolution of MSS with 500 km swath resolution."},"28945":{"color":"#AAAAAA","type":"Telecommunications","launch_date":"11 March 2006, 22:33 GMT","mission_duration":"Planned 15 years","launch_mass":"3,683 kg (8,120 lb)","isGeoStationary":true,"tle_line1":"1 28945U 06007A   20268.07390940 -.00000236  00000-0  00000-0 0  9993","tle_line2":"2 28945   0.0308 230.5603 0005379 321.3166 168.1194  1.00272069 53247","name":"SPAINSAT","sat_name":"SPAINSAT","country_name":"Spain","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Spain.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon4.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Spainsat_1jpg.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Spainsat_2.jpg"],"use_cases":["The satellite provides coverage to humanitarian, security and intelligence missions as well as to military operations, image transfers, embassy services and Spanish governmental communications."],"description":"Spainsat is a Spanish telecommunications satellite used for military and government communications. It allows telecommunications the different missions of the Spanish Armed forces abroad by providing coverage on a wide area of the world ranging from the United States and South America to the Middle East, including Africa and Europe.The satellite was launched on 11 March 2006 at 22:33 GMT from the Guiana Space Centre in Kourou, French Guiana, by an Ariane 5 ECA rocket of Arianespace."},"29108":{"color":"#8811FF","mission_duration":"Elapsed: 14 years, 5 months, 1 day","launch_mass":"587 kilograms (1,294 lb)","type":"Earth observation","launch_date":"April 28, 2006, 10:02:16 UTC","isGeoStationary":false,"tle_line1":"1 29108U 06016B   20272.95708466  .00000169  00000-0  42776-4 0  9997","tle_line2":"2 29108  98.2386 219.6807 0001264  82.7286 277.4059 14.62442809767449","name":"CALIPSO","sat_name":"CALIPSO","country_name":"USA","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/america.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon9.png","description":"CALIPSO is a joint NASA (USA) and CNES (France) environmental satellite, built in the Cannes Mandelieu Space Center, which was launched atop a Delta II rocket on April 28, 2006. Its name stands for Cloud-Aerosol Lidar and Infrared Pathfinder Satellite Observations.Passive and active remote sensing Instruments on board the CALIPSO satellite monitor aerosols and clouds 24 hours a day. CALIPSO is part of the \"A Train\", flying in formation with several other satellites (Aqua, Aura and CloudSat).","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Calipso.png","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Calipso_2.jpg"],"use_cases":["Provide statistics on the vertical structure of clouds around the globe (both missions)","Provide statistics on the geographic and vertical distribution of aerosols around the globe (CALIPSO)","Detect snowfall from space (CloudSat)"]},"29155":{"color":"#0AF90D","mission_duration":"10 years (planned) 14 years, 3 months and 23 days (elapsed)","launch_mass":"3,133 kilograms (6,907 lb)","launch_date":"24 May 2006, 22:11:00 UTC","type":"Weather Satellite","isGeoStationary":true,"tle_line1":"1 29155U 06018A   20258.41160618  .00000029  00000-0  00000-0 0  9992","tle_line2":"2 29155   0.0882 146.2752 0008420  58.7161 358.3120  1.00273659 52430","name":"Geostationary Operational Environmental Satellite system","sat_name":"GOES-13","country_name":"USA","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/america.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon5.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/GOES-13_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/GOES-13_2.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/GOES-13_3.jpg"],"use_cases":["An improved Image Navigation and Registration (INR) system that uses star trackers,  optical devices that measures the position of the stars to help with spacecraft orientation, to provide more stable and accurate Earth-pointing capabilities, decreasing the amount of “shake” in an image.","A sounder that provides detailed descriptions of conditions in the atmosphere at any time, allowing meteorologists to deduce atmospheric temperature and moisture profiles, surface and cloud-top temperatures, and ozone distributions. Sounder data is also used in computer models that produce mid-and long-range weather forecasts.","An imaging radiometer that uses data obtained from its five channels to continuously produce images of the Earth’s surface, oceans, severe storm development, cloud cover, cloud temperature and height, surface temperature, and water vapor. It allows users to identify fog at night, distinguish between water and ice clouds during daytime hours, detect hot spots (such as volcanoes and forest fires), locate a hurricane eye, and acquire measurements of ground and sea surface temperatures."],"description":"EWS-G1 (Electro-optical infrared Weather System - Geostationary 1) is a weather satellite of the U.S. Space Force, formerly GOES-13 (also known as GOES-N before becoming operational) and part of the National Oceanic and Atmospheric Administration's Geostationary Operational Environmental Satellite system. On 14 April 2010, GOES-13 became the operational weather satellite for GOES-East.GOES-N was launched aboard a Boeing Delta IV-M+(4,2) rocket, flying from SLC-37B at the Cape Canaveral. The launch occurred at 22:11:00 UTC on 24 May 2006.its mission mass was 3133 Kg"},"29479":{"color":"#888899","type":"Solar mission","launch_date":"21:36:00, September 22, 2006 (UTC","mission_duration":"Elapsed: 13 years, 11 months and 28 days","launch_mass":"700.0 kg","isGeoStationary":false,"tle_line1":"1 29479U 06041A   20262.51164999  .00000035  00000-0  15547-4 0  9992","tle_line2":"2 29479  98.1596 273.5712 0018431  77.0511 283.2749 14.65085332747512","name":"Solar-B","sat_name":"HINODE (SOLAR-B)","country_name":"Japan","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Japan.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon5.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Hinode_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Hinode_2.jpg"],"use_cases":["Hinode explores the magnetic fields of the Sun in order to improve understanding of what powers the solar atmosphere and drives solar eruptions"," Hinode's Solar Optical Telescope is the first space-borne instrument to measure the strength and direction of the Sun's magnetic field on the Sun's surface, the photosphere."],"description":"Hinode (/ˈhiːnoʊdeɪ/; Japanese: ひので, IPA: [çinode], Sunrise), formerly Solar-B, is a Japan Aerospace Exploration Agency Solar mission with United States and United Kingdom collaboration. It is the follow-up to the Yohkoh (Solar-A) mission and it was launched on the final flight of the M-V rocket from Uchinoura Space Center, Japan on 22 September 2006 at 21:36 UTC (23 September, 06:36 JST). Initial orbit was perigee height 280 km, apogee height 686 km, inclination 98.3 degrees. Then the satellite maneuvered to the quasi-circular sun-synchronous orbit over the day/night terminator, which allows near-continuous observation of the Sun. On 28 October 2006, the probe's instruments captured their first images.\nThe data from Hinode are being downloaded to the Norwegian, terrestrial Svalsat station, operated by Kongsberg a few kilometres west of Longyearbyen, Svalbard. From there, data is transmitted by Telenor through a fibre-optic network to mainland Norway at Harstad, and on to data users in North America, Europe and Japan."},"29648":{"color":"#AA0011","type":"Communications","launch_date":"11 December 2006, 23:28:43 UTC","mission_duration":"15 years","launch_mass":"4,765 kilograms (10,505 lb)","isGeoStationary":true,"tle_line1":"1 29648U 06056A   20261.45542586 -.00000255  00000-0  00000-0 0  9999","tle_line2":"2 29648   0.0262 154.2160 0003807  16.2272  81.8405  1.00268104 29423","name":"Malaysian communications satellite","sat_name":"MEASAT-3 ","country_name":"Malaysia","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Malaysia.jpg","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon2.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/MEASAT-3_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/MEASAT-3_2.jpg"],"use_cases":["MEASAT-3 employs 24 C-band and 24 Ku-band transponders, each providing 36 MHz of bandwidth over a 15-year minimum service life.","MEASAT-3 C-Band payload will cover Africa, Middle East, Eastern Europe, Asia and Australia. The Ku Band payload has been designed to provide high-powered flexible service options for the development of data services and Direct-to-Home applications in Malaysia, China and India.\n"],"description":"MEASAT-3 is a Malaysian communications satellite which was successfully launched on 11 December 2006 from the Baikonur Cosmodrome in Kazakhstan.In March 2003, MEASAT Satellite Systems Sdn. Bhd. of Malaysia ordered a Boeing 601HP satellite, giving it the designation MEASAT-3 at that time. MEASAT-3 joined the existing Boeing-built MEASAT-1 and MEASAT-2 spacecraft in the Malaysia-East Asia Satellite (MEASAT) system.MEASAT-3 was by International Launch Services using a Proton-M rocket with a Briz-M upper stage. The upper stage made five burns to place MEASAT-3 into a geostationary transfer orbit. After circularisation and testing, the satellite entered commercial service on January 25, 2007, in geostationary orbit at a longitude of 91.5 degrees East where it is co-located with MEASAT-1. The C band and Ku band now covers geographically remote areas such as Sabah, Sarawak, and North India and expands the MEASAT fleet's coverage to more than 100 countries embracing Australia, Middle East, Eastern Europe and Africa."},"31135":{"color":"#0FA0FF","type":"Gamma-ray astronomy","launch_date":"23 April 2007, 10:00:00 UTC","mission_duration":"until 2010 (original); operational as of 2018","launch_mass":"352 kilograms (776 lb)","isGeoStationary":false,"tle_line1":"1 31135U 07013A   20263.02185652  .00002134  00000-0  30340-4 0  9990","tle_line2":"2 31135   2.4667 182.1691 0011966 200.9479 159.0084 15.32518178746423","name":"Astro‐Rivelatore Gamma a Immagini Leggero","sat_name":"AGILE","country_name":"Italy","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Italy.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon5.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Agile_2.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Agile_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Agile_3.jpg"],"use_cases":["AGILE's mission is to observe gamma-ray sources in the universe."],"description":"AGILE (Astro‐Rivelatore Gamma a Immagini Leggero) is an X-ray and Gamma ray astronomical satellite of the Italian Space Agency (ASI).AGILE (Astro rivelatore Gamma a Immagini Leggero) is an Italian high-energy astrophysics mission dedicated to the observation of the gamma-ray Universe. Its very innovative instrumentation is unprecedentedly light (100 kg) and the most compact ever operational for high-energy astrophysics (approximately a cube of about 60 cm size) with excellent detection and imaging capability. Satellite data are collected by the ASI ground station in Malindi (Kenya), then quickly transferred to the Satellite Operations Centre in Fucino, transferred, preprocessed, and stored and analyzed at the ASI Science Data Center (ASDC) in Frascati. In parallel the pre-processed data are transferred at INAF/OAS Bologna for a fast science alert generation, thus assuring a very rapid response to gamma-ray detections, obtained by special quick look analysis programs and coordinated ground-based and space observations.AGILE was successfully launched on 23 April 2007, from the Indian base of Sriharikota and was inserted in an equatorial orbit with low particle background. On 23 April 2007, ASI made contact with AGILE; its signals were acquired by the ground station at the Broglio Space Centre near Malindi, Kenya and it was placed in a Sun-pointing mode."},"33051":{"color":"#00DDAA","type":"Communication","launch_date":"9 June 2008, 12:15 UTC","mission_duration":"15 years","launch_mass":"4,500 kilograms (9,900 lb)","isGeoStationary":true,"tle_line1":"1 33051U 08028A   20270.42842598 -.00000271  00000-0  00000-0 0  9994","tle_line2":"2 33051   0.0135 181.5164 0003797   5.3187  65.3015  1.00271500 45082","name":"CHINASAT 9","country_name":"China","sat_name":"CHINASAT 9 (ZX 9)","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/China.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon5.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/ChinaSat_9_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/ChinaSat_9_2.jpg"],"use_cases":["It will  broadcast satellite services (BSS), including eighteen 36 MHz and four 54 MHz channels."],"description":"ChinaSat 9 (Chinese: 中星9号; pinyin: Zhōngxīng Jiǔhào),also known as ZX-9,[citation needed] is a Chinese communications satellite. It was launched from pad 2 at the Xichang Satellite Launch Centre on 9 June 2008, at 12:15 GMT, by a Long March 3B carrier rocket.It is based on the Spacebus 4000C2 satellite bus, and was constructed in France by Thales Alenia Space in its Cannes Mandelieu Space Center.It is one of several ChinaSat spacecraft in orbit.It was launched to act as a relay satellite for the 2008 Olympic Games, and will subsequently be used for general communications. Equipped with 22 NATO J-band (IEEE Ku band) transponders, it was placed in geosynchronous orbit at a longitude of 92.2° East."},"33591":{"color":"#EC0EDB","launch_mass":"1440 Kg","type":"Weather Satellite","launch_date":"February 6, 2009, 10:22:00 UTC","mission_duration":"2 years (planned) Elapsed: 11 years, 7 months, 9 days","isGeoStationary":false,"tle_line1":"1 33591U 09005A   20257.50350328  .00000028  00000-0  40745-4 0  9991","tle_line2":"2 33591  99.1971 265.1462 0013865 185.1027 175.0005 14.12431210597820","name":"National Oceanic and Atmospheric Administration Satellite","sat_name":"NOAA 19","country_name":"USA","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/america.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon4.png","description":"NOAA-19, designated NOAA-N' (NOAA-N Prime) prior to launch,is the last of the United States National Oceanic and Atmospheric Administration's POES series of weather satellites.NOAA-19 was launched on February 6, 2009.It is a weather satellite.It was launched by Delta II 7320-10C rocket.Its mission is for 15 years.Its launch mass was 1140 Kg.Its launch site was Vandenberg SLC-2W","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/NOAA-19_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/NOAA_19_2.jpg"],"use_cases":["NOAA-19 will provide a polar-orbiting platform to support environmental monitoring instruments for imaging and measuring Earth's atmosphere and sea surface temperature.","The NOAA-19 satellite, built for NASA by Lockheed Martin, will improve weather forecasting and monitor environmental events around the world.","The NOAA-19 satellite will collect meteorological data and transmit the information to NOAA's Satellite and Information Service, which processes the data for input to the National Weather Service for its long-range weather and climate forecasts. Forecasters worldwide also will be able to access the satellite's images and data. NOAA-19 has sensors that will be used in the Search and Rescue Satellite-Aided Tracking System to monitor for distress signals around the world."]},"35683":{"color":"#FAB304","type":"Optical imaging Disaster monitoring","launch_date":"29 July 2009, 18:46:29 UTC","mission_duration":"5 years (expected)","launch_mass":"120 kilograms (260 lb)","isGeoStationary":false,"tle_line1":"1 35683U 09041C   20260.45590854  .00000037  00000-0  13470-4 0  9998","tle_line2":"2 35683  97.6952  98.0246 0001632 101.5813 258.5597 14.71159926597552","name":"British Earth imaging satellite ","sat_name":"UK-DMC 2","country_name":"UK","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/UK.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon3.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/uk-dmc-2_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/uk-dmc_2.jpg"],"use_cases":["As part of the Disaster Monitoring Constellation, UK-DMC-2 will be used to maintain rapid revisit times needed to monitor fast changing phenomena such as fires, floods and crops.","Furthermore it also covers large areas quickly including the Amazon Basin, Australia, and Europe, providing the capability for multi-season coverage of major agriculture. To enhance the capability of imaging very large areas as quickly as possible and being able to offer custom data delivery contracts to customers, a number of operational modes are implemented "],"description":"UK-DMC 2 is a British Earth imaging satellite which is operated by DMC International Imaging.It was constructed by Surrey Satellite Technology, based on the SSTL-100 satellite bus.It is part of Britain's contribution to the Disaster Monitoring Constellation, which is coordinated by DMC International Imaging. It is the successor to the UK-DMC satellite.UK DMC-2 was launched into a sun-synchronous low Earth orbit. The launch was conducted by ISC Kosmotras, using a Dnepr carrier rocket, with DubaiSat-1 being the primary payload. UK-DMC 2, along with the Deimos-1, Nanosat 1B, AprizeSat-3 and AprizeSat-4 satellites, were the rocket's secondary payload. The launch occurred at 18:46 GMT on 29 July 2009, with the rocket lifting off from Site 109/95 at the Baikonur Cosmodrome in Kazakhstan."},"35756":{"color":"#AAdd00","type":"Communication","launch_date":"21 August 2009, 22:09 UTC","mission_duration":"15 years","launch_mass":"2,401 kilograms (5,293 lb)","isGeoStationary":true,"tle_line1":"1 35756U 09044B   20271.47500795 -.00000170  00000-0  00000+0 0  9990","tle_line2":"2 35756   0.0117  45.6716 0002532 132.7555 155.2938  1.00270160 40695","name":"OPTUS D3","sat_name":"OPTUS D3","country_name":"Australia","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Australia.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon4.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Optus_D3_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Optus_D3_2.jpg"],"use_cases":["Optus D3 operates over Australia in the BSS band and is co-located with the Optus C1 satellite to provide enhanced capability for the Australia’s prime orbital location delivering direct-to-home services across Australia","Optus D3 also provides FSS band back up capacity to New Zealand. Optus D3 provides capacity for growth to meet business demands and increases the resilience of our satellite network for mission critical applications."],"description":"Optus D3 is an Australian geostationary communications satellite, which is operated by Optus and provides communications services to Australasia. D3 was the third Optus-D satellite to be launched. It is a 2,401-kilogram (5,293 lb) satellite, which was constructed by Orbital Sciences Corporation based on the Star-2.4 satellite bus, with the same configuration as the earlier Optus D2 satellite.It was launched, along with the Japanese JCSAT-12 satellite, by Arianespace. An Ariane 5ECA rocket was used for the launch, which occurred from ELA-3 at the Guiana Space Centre in Kourou, French Guiana. The launch took place at 22:09 GMT on 21 August 2009, at the start of a 60-minute launch window."},"35873":{"color":"#55AAdd","type":"Communications","launch_date":"17 September 2009, 19:19:19 UTC","mission_duration":"15 years","launch_mass":"4,745 kilograms (10,461 lb)","isGeoStationary":true,"tle_line1":"1 35873U 09050A   20272.47719444 -.00000278  00000-0  00000-0 0  9991","tle_line2":"2 35873   0.0206 121.8854 0003442  69.2454 275.6750  1.00272591 40427","name":"NIMIQ 5","sat_name":"NIMIQ 5","country_name":"Canada","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Canada.jpg","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon4.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Nimiq-5_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Nimiq-5_2.jpg"],"use_cases":["The satellite has entire capacity to provide High Definition television direct-to-home broadcasting for Dish Network Corporation."],"description":"Nimiq 5 is a Canadian communications satellite, operated by Telesat Canada as part of its Nimiq fleet of satellites.It is positioned in geostationary orbit at a longitude of 72.7° West of the Greenwich Meridian. As of July 2015, EchoStar Corporation leases the satellite's entire capacity to provide High Definition television direct-to-home broadcasting for Dish Network Corporation. When accessed using a multi-satellite receiver such as the VIP722k and a multi-satellite dish/LNB combo, such as the Dish-300, Dish-500, or Dish-Turbo 1000.4, the satellite is (incorrectly) referred to by the on-screen diagnostics as Echostar 72W.Nimiq 5 was launched by International Launch Services, using a Proton-M rocket with a Briz-M upper stage, under a contract signed in April 2007.[10] The launch was conducted from Site 200/39 at the Baikonur Cosmodrome in Kazakhstan, at 19:19 GMT on 17 September 2009. The Briz-M separated from the Proton-M nine minutes and forty one seconds into the flight and subsequently made five burns before releasing Nimiq 5 into a geosynchronous transfer orbit nine hours and fifteen minutes after liftoff"},"35932":{"color":"#AA99FF","type":"Atmospheric Technology","launch_date":"23 September 2009, 06:21 UTC","mission_duration":"3-12 months planned\n84+ months achieved","launch_mass":"1 kilogram (2.2 lb)","isGeoStationary":false,"tle_line1":"1 35932U 09051B   20268.49126900  .00000115  00000-0  36995-4 0  9999","tle_line2":"2 35932  98.6124  93.9733 0007535 174.9742 185.1532 14.56426008584246","name":"SWISSCUBE-1","sat_name":"SWISSCUBE-1","country_name":"Switzerland","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Switzerland.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon5.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Swisscube_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Swisscube_2.jpg"],"use_cases":["It has also been used for amateur radio.","The spacecraft is a single unit CubeSat, which was designed to conduct research into nightglow within the Earth's atmosphere, and to develop technology for future spacecraft"],"description":"SwissCube-1 is a Swiss satellite operated by Ecole Polytechnique Fédérale de Lausanne (EPFL). The spacecraft is a single unit CubeSat, which was designed to conduct research into nightglow within the Earth's atmosphere, and to develop technology for future spacecraft.It has also been used for amateur radio. It was the first Swiss satellite to be launched.SwissCube-1 was launched by the Indian Space Research Organisation aboard the Polar Satellite Launch Vehicle, serial number C14, flying in the Core Alone, or PSLV-CA, configuration.The launch took place from the First Launch Pad at the Satish Dhawan Space Centre, at 06:21 UTC on 23 September 2009.SwissCube-1 was a secondary payload aboard the rocket, which deployed the Oceansat-2 satellite. Five other secondary payloads were flown aboard the rocket; BeeSat, UWE-2, ITU-pSat1, Rubin 9.1 and Rubin 9.2."},"36744":{"color":"#AC5519","type":" Communication Oceanography Weather","launch_date":"26 June 2010, 21:41 UTC","mission_duration":"7 years","launch_mass":"2,460 kilograms (5,420 lb)","isGeoStationary":true,"tle_line1":"1 36744U 10032A   20260.48591833 -.00000357  00000-0  00000+0 0  9996","tle_line2":"2 36744   0.0105 124.2104 0000612  75.1398  99.6359  1.00270401 37598","name":"Chollian","sat_name":"COMS 1","country_name":"South Korea ","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/South_Korea.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon3.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/COMS-1_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/COMS-1_2.jpg"],"use_cases":[" East. It features an experimental Ka-band communications package","A  weather observation system and equipment to study ocean color and the marine ecosystem to aid the fishing industry.  "],"description":"Chollian, (Korean, lit. Thousand Li View) also known as Communication, Ocean and Meteorological Satellite 1 (COMS-1), is a South Korean satellite which was launched in June, 2010. It will be operated by the Korea Aerospace Research Institute, who will use it for communication, oceanography, and meteorological observation.COMS-1 was launched by Arianespace using an Ariane 5ECA carrier rocket lifting off from ELA-3 at the Guiana Space Centre in Kourou, French Guiana. The first attempt to launch it occurred on 23 June 2010, however the launch was scrubbed due to a problem with one of the rocket's subsystems.A subsequent attempt on 24 June was also scrubbed, due to a problem with the pressurisation of the rocket's fuel tanks.The launch occurred at 21:41 UTC on 26 June 2010."},"37779":{"color":"#A07335","type":"Geosynchronous and Communication","launch_date":"11 August 2011, 16:15:04 UTC[2]","mission_duration":"12 years","launch_mass":"5,115 kilograms (11,277 lb)[1]","isGeoStationary":true,"tle_line1":"1 37779U 11042A   20261.27410670  .00000140  00000-0  00000+0 0  9993","tle_line2":"2 37779   0.0558 308.2868 0003149 166.6548  18.3948  1.00268699 33236","name":"Advanced geosynchronous and communications satellite ","sat_name":"Paksat-1R","country_name":"Pakistan","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Pakistan.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon4.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Paksat-1_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Paksat-1_2.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Paksat-1_3.jpg"],"use_cases":["The PakSat-1 offers the C and Ku band coverage in over 75 countries across Europe, Africa, Middle East, South and Central Asia","PAKSAT enables businesses highly cost-effective means to deploy mission critical applications, regardless of the location and remoteness of their offices with applications like Web access, corporate networks and Voice over IP.","PAKSAT enables businesses highly cost-effective means to deploy mission critical applications, regardless of the location and remoteness of their offices with applications like Web access, corporate networks and Voice over IP."],"description":"PakSat-1R (or PakSat-1 Replacement) is an advanced geosynchronous and communications satellite that was manufactured by China Great Wall Industry Corporation (CGWIC) and operated by the Space and Upper Atmosphere Research Commission (Suparco), an executive space authority of the government of Pakistan.[6] The PakSat-1R was developed and built as a geostationary telecommunications satellite and was launched from Xichang, China, at 16:15 UTC on 11 August 2011. Launched on the Long March 3B rocket, the satellite has a design life of 15 years with initial goals to provide broadband internet access, digital television broadcasting, remote and rural telephony, emergency communications, tele-education and tele-medicine services across South and Central Asia, Eastern Europe, East Africa and the Far East. The satellite successfully took over the operations of its predecessor, the PakSat-1E satellite leased by Pakistan, in geostationary orbit at 38° East"},"37838":{"color":"#FFFF00","type":"Weather ","launch_date":"12 October,2011","mission_duration":"3 years(planned) ","launch_mass":"1,000 kilograms (2,205 lb)","isGeoStationary":false,"tle_line1":"1 37838U 11058A   20259.18693167  .00000280  00000-0  91537-5 0  9993","tle_line2":"2 37838  19.9767 190.4780 0009555 107.6454 252.4994 14.09698775393411","name":"MEGHA-TROPIQUES","sat_name":"MEGHA-TROPIQUES","country_name":"India","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/India.jpg","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon5.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Megha-Tropiques_1.jpeg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Megha-Tropiques_2.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Megha_Tropiques_3.jpg"],"use_cases":["Microwave Analysis and Detection of Rain and Atmospheric Structures (MADRAS), an Imaging Radiometer developed jointly by CNES and ISRO","Sounder for Probing Vertical Profiles of Humidity (SAPHIR),from CNES.","Radio Occultation Sensor for Vertical Profiling of Temperature and Humidity (ROSA), procured from Italy","Scanner for Radiation Budget (ScaRaB),from CNES"],"description":"Megha-Tropiques is a satellite mission to study the water cycle in the tropical atmosphere in the context of climate change A collaborative effort between Indian Space Research Organisation (ISRO) and French Centre National d’Etudes Spatiales (CNES),Megha-Tropiques was successfully deployed into orbit by a PSLV rocket in October 2011.The Megha-Tropiques satellite was successfully placed in an 867 km orbit with an inclination of 20 degrees to the equator by the Indian Space Research Organisation through its Polar Satellite Launch Vehicle (PSLV-C18) on October 12, 2011.The PSLV-C18 was launched at 11:00 am on October 12,2011 from the first launch pad of the Satish Dhawan Space Centre (SHAR) located in Sriharikota, Andhra Pradesh. The satellite was placed in orbit along with three micro satellites: the 10.9 kg SRMSAT built by the SRM University,Chennai, the 3 kg remote sensing satellite Jugnu from the Indian Institute of Technology Kanpur(IIT Kanpur) and the 28.7 kg VesselSat-1 of Luxembourg to locate ships on high seas."},"37843":{"color":"#FF0000","type":"Communication","launch_date":"19 October 2011, 18:48:58 UTC","mission_duration":"15 years","launch_mass":"6,740 kg (14,860 lb)","isGeoStationary":true,"tle_line1":"1 37843U 11059A   20261.53043517 -.00000037  00000-0  00000-0 0  9991","tle_line2":"2 37843   0.0189 192.9399 0002171 340.6803 259.1555  1.00271051 26278","name":"high throughput communications satellite ","sat_name":"VIASAT-1","country_name":"Canada","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Canada.jpg","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon2.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/ViaSat-1_1jpg.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/ViaSat-1_2.jpg"],"use_cases":["The objective is to create a better satellite broadband user experience","making satellite competitive with DSL and wireless broadband alternatives for the first time."],"description":".ViaSat-1 is a high throughput communications satellite owned by ViaSat Inc. and Telesat Canada.Launched October 19, 2011 aboard a Proton rocket, it held the Guinness record for the world's highest capacity communications satellite with a total capacity in excess of 140 Gbit/s, more than all the satellites covering North America combined, at the time of its launch.The satellite is positioned at 115.1 degrees West longitude geostationary orbit point, with 72 Ka-band spot beams; 63 over the U.S. (Eastern and Western states, Alaska and Hawaii), and nine over Canada. "},"37849":{"color":"#AA8811","type":"Meteorology","launch_date":"28 October 2011, 09:48:01.828 UTC","mission_duration":"5 years","launch_mass":"2,128 kilograms (4,691 lb)","isGeoStationary":false,"tle_line1":"1 37849U 11061A   20262.52421558  .00000008  00000-0  24459-4 0  9990","tle_line2":"2 37849  98.7405 199.2161 0001160  62.0035  57.3782 14.19544217460830","name":"NPOESS Preparatory Project (NPP)","sat_name":"SUOMI NPP [+]","country_name":"USA","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/america.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon5.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Suomi-Npp_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Suomi-Npp_2.jpg"],"use_cases":["Climate change -- contribute to long-term records of global environmental data critical for understanding the dynamics of climate change","Health of the ozone layer -- daily measurements of the atmospheric ozone layer that will determine whether the ozone layer is recovering as expected","Weather predictions -- a sounding instrument will collect information about cloud cover, atmospheric temperatures, humidity and other variables critical to accurate weather prediction","Temperatures -- maintain a global record of atmospheric, land surface and sea surface temperatures critical to understanding the long-term dynamics of climate change"],"description":"NPOESS Preparatory Project (NPP) is intended to bridge the gap between old EOS and new systems by flying new instruments, on a new satellite bus, using a new ground data network. Originally planned for launch five years earlier as a joint NASA/NOAA/DOD project, NPP was to be a pathfinder mission for the larger National Polar-orbiting Operational Environmental Satellite System until DOD participation in the larger project was dissolved. The project continued as a civilian weather forecasting replacement for the NOAA Polar Operational Environmental Satellites (POES) series, and ensured continuity of climate measurements begun by NASA's Earth Observing System.The satellite was launched from Space Launch Complex 2W at Vandenberg Air Force Base in California by a United Launch Alliance Delta II 7920-10C on October 28, 2011. The satellite was placed into a sun-synchronous orbit 824 km (512 miles) above the Earth."},"39035":{"color":"#FF00dd","type":"Satellite Communications","launch_date":"18:50, December 19, 2012 (UTC)","mission_duration":"15 years","launch_mass":"2,935 kilograms (6,471 lb)","isGeoStationary":true,"tle_line1":"1 39035U 12075B   20272.61937951 -.00000046  00000-0  00000-0 0  9990","tle_line2":"2 39035   0.0192 167.0073 0002895 357.3663 311.6478  1.00272728 28251","name":"Mexsat Bicentenario","sat_name":"Mexsat-3","country_name":"Mexico","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Mexico.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon5.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Mexsat-3.jpg"],"use_cases":["The Ministry of Communications and Transportation said in a statement that the new satellite would provide fixed broadband services for access to the Internet, digital high-quality satellite telephony, videoconferencing, remote medical care and education via television."," It said that Bicentenario would provide services for emergency care \"before, during and after emergencies\""],"description":"Mexsat-3, also known as Mexsat Bicentenario or simply Bicentenario, is the first of three Mexican satellites forming the MEXSAT telecommunications network, and is named to commemorate the bicentennial of the Independence of Mexico. It was launched on 19 December 2012 to serve the other two satellites in the network, Mexsat-1 and Mexsat-2, as a fixed satellite service. It was manufactured by the company Orbital Sciences Corporation and was launched from Kourou in French Guiana, and currently occupies the orbit 114.9° West.The French company Arianespace launched an Ariane 5 rocket carrying the Mexsat-3 satellite from the Guiana Space Centre near Kourou, French Guiana at 18:50 local time (21:50 GMT) as part of a previously signed contract. The rocket reached the 100 kilometre mark (the distance from Earth which can be considered an orbit) in the first three minutes.The satellite was placed in a high geostationary orbit 36,000 kilometres above the surface."},"39084":{"color":"#FA04EB","type":"Satellite imagery","launch_date":"11 February 2013, 18:02:00 UTC","mission_duration":"5 years (planned) 10 years (with fuel) Elapsed: 7 years, 7 months, 4 days","launch_mass":"2,623 kilograms (5,783 lb)","isGeoStationary":false,"tle_line1":"1 39084U 13008A   20261.48469765  .00000020  00000-0  14485-4 0  9994","tle_line2":"2 39084  98.2187 330.0119 0001234  77.2800 282.8539 14.57106333404101","name":" American Earth observation satellite ","sat_name":"LANDSAT 8","country_name":"USA","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/america.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon2.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/landsat-8_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/landsat-8_2.jpg"],"use_cases":[" It is collecting valuable data and imagery used in agriculture, education, business, science, and government.","The Landsat Program provides repetitive acquisition of high resolution multispectral data of the Earth’s surface on a global basis","The data from Landsat spacecraft constitute the longest record of the Earth’s continental surfaces as seen from space. It is a record unmatched in quality, detail, coverage, and value"],"description":"Landsat 8 is an American Earth observation satellite launched on 11 February 2013. It is the eighth satellite in the Landsat program; the seventh to reach orbit successfully. Originally called the Landsat Data Continuity Mission (LDCM), it is a collaboration between NASA and the United States Geological Survey (USGS). NASA Goddard Space Flight Center in Greenbelt, Maryland, provided development, mission systems engineering, and acquisition of the launch vehicle while the USGS provided for development of the ground systems and will conduct on-going mission operations. It comprises the camera of the Operational Land Imager (OLI) and the Thermal Infrared Sensor (TIRS) which can be used to study earth surface temperature and is used to study global warming.The satellite was built by Orbital Sciences Corporation, who served as prime contractor for the mission. The spacecraft's instruments were constructed by Ball Aerospace & Technologies and NASA's Goddard Space Flight Center (GSFC), and its launch was contracted to United Launch Alliance.During the first 108 days in orbit, LDCM underwent checkout and verification by NASA and on 30 May 2013 operations were transferred from NASA to the USGS when LDCM was officially renamed to Landsat 8"},"39150":{"color":"#FFA500","type":"Earth observation ","launch_date":"26 April 2013, 04:13:00 UTC","mission_duration":"5~8 years","launch_mass":"1080 kg","isGeoStationary":false,"tle_line1":"1 39150U 13018A   20259.58735355 -.00000006  00000-0  63993-5 0  9992","tle_line2":"2 39150  97.8628 334.0907 0018864 183.2314 176.8783 14.76555795398313","name":"High resolution - number 1 ","sat_name":"GAOFEN 1 ","country_name":"China","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/China.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon4.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Gaofen-1_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Gaofen-1_2.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Gaofen-1_3.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Gaofen-1_4.jpg"],"use_cases":["Agricultural planning","Geographical mapping, environmental and resource surveying","Maritime surveillance, and national security.","Disaster relief and climate change monitorin"],"description":"Gaofen 1 (Chinese: 高分一号; pinyin: Gāofēn Yī hào; lit.: 'high resolution - number 1') is a Chinese high-resolution Earth observation satellite, and the first of the Gaofen series satellites.The civilian CHEOS (China High-resolution Earth Observation System) satellite program was proposed in 2006 and received approval in 2010. Gaofen 1 was the first of six planned CHEOS spacecraft for being launched between 2013 and 2016.Gaofen 1 was launched on 26 April 2013 at 04:13:00 UTC with a Long March 2D carrier rocket from the Jiuquan Satellite Launch Center along with the three small experimental satellites: TurkSat-3USat (Turkey), CubeBug 1 (Argentina) and NEE-01 Pegaso (Ecuador) in a sun-synchronous orbit."},"39215":{"color":"#009900","type":"Communications\nTechnology","launch_date":"25 July 2013, 19:54:07 UTC","mission_duration":"Expected: 15 years Elapsed: 7 years, 1 month, 23 days","launch_mass":"6,649 kilograms (14,659 lb)","isGeoStationary":true,"tle_line1":"1 39215U 13038A   20263.09310035  .00000122  00000-0  00000-0 0  9994","tle_line2":"2 39215   2.4845  19.4840 0002193 167.2366 230.0957  1.00270761 23024","name":"Inmarsat-4A F4","sat_name":"ALPHASAT","country_name":"UK","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/UK.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon5.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Alphasat_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Alphasat_2.jpg"],"use_cases":["Develop and qualify, together with CNES (Centre National d'Etudes Spatiales) and industry, a generic product line for Alphabus based on ground qualification of development and engineering models","Procure a flight-standard platform (protoflight model) to facilitate early first flight opportunity (in the frame of Alphasat).","Consolidate a Large Platform Mission combining ESA-funded payloads with institutional and/or commercial payloads, allowing the in-orbit demonstration of a protoflight large platform."],"description":"Inmarsat-4A F4, also known as Alphasat and Inmarsat-XL, is a large geostationary communications I-4 satellite operated by UK based Inmarsat in partnership with the European Space Agency. Launched in 2013, it is used to provide mobile communications to Africa and parts of Europe and Asia.Inmarsat-4A F4 has been constructed by EADS Astrium and Thales Alenia Space based on the Alphabus satellite bus. It was the first Alphabus spacecraft to be launched, and as such it carries several experimental communications systems in addition to its commercial payload. The spacecraft had a launch mass of 6,649 kilograms (14,659 lb), and is expected to operate for at least fifteen years.The satellite was used as part of a in-orbit verification of the ESA European Data Relay System. In 2014, data from the Sentinel-1A satellite in LEO was transmitted via an optical link to the Alphasat in GEO and then relayed to a ground station using a Ka band downlink. The new system can offer speeds up to 7.2 Gbit/s in the future"},"39253":{"color":"#AA00FF","type":"Ultraviolet astronomy","launch_date":"14 September 2013, 05:00 UTC","mission_duration":"~1 year planned (science phase)\nelapsed: 7 years and 8 days","launch_mass":"340 kg (750 lb)","isGeoStationary":false,"tle_line1":"1 39253U 13049A   20265.49464547  .00000121  00000-0  76280-4 0  9998","tle_line2":" 39253  29.7209  66.2307 0137480 319.9343  39.1158 13.55228815348082","name":"HISAKI ","sat_name":"HISAKI (SPRINT-A)","country_name":"Japan","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Japan.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon4.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Hisaki_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Hisaki_2.jpg"],"use_cases":["Objectives. Hisaki carries an extreme ultraviolet spectrometer which will be used to study the composition of the atmospheres and the behavior of the magnetospheres of the planets of the Solar System."],"description":"An Epsilon was used to launch Hisaki. Making its first flight, the four-stage Epsilon rocketflew from the Mu rocket launch complex at the Uchinoura Space Centre. The launch occurred at 05:00 UTC on 14 September 2013, following a scrubbed launch attempt on 27 August 2013. Following its successful insertion into orbit and deployment of its solar arrays, the satellite was renamed Hisaki, having been designated SPRINT-A until that pointHisaki, also known as the Spectroscopic Planet Observatory for Recognition of Interaction of Atmosphere (SPRINT-A) is a Japanese ultraviolet astronomy satellite operated by the Japan Aerospace Exploration Agency (JAXA). The first mission of the Small Scientific Satellite programme, it was launched in September 2013 on the maiden flight of the Epsilon rocket. Hisaki remains operational as of 2017, and is performing joint observations with Juno orbiter. Hisaki was named after a cape Hisaki (火崎, literally Cape Fire) used by local fishermen to pray for safe travels in the eastern part of Kimotsuki, Kagoshima near the Uchinoura Space Center, but has the additional meaning of \"beyond the Sun\". An old name for the mission was EXCEED (Extreme Ultraviolet Spectroscope for Exospheric Dynamics)."},"39265":{"color":"#E692A9","type":"Technology Communications Research","launch_date":"September 29, 2013, 16:00 UTC","mission_duration":"Primary mission: 18 months Design life: 2 years Elapsed: 6 years, 11 months, 18 days","launch_mass":"500 kg (1,100 lb)","isGeoStationary":false,"tle_line1":"1 39265U 13055A   20259.88296471  .00004148  00000-0  11521-3 0  9992","tle_line2":"2 39265  80.9668 219.8605 0659782  38.5582 326.0858 14.29826774360723","name":"Cascade, Smallsat and Ionospheric Polar Explorer (CASSIOPE)","sat_name":"CASSIOPE","country_name":"Canada","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Canada.jpg","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon5.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/CASSIOPE_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/CASSIOPE_2.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/CASSIOPE_3.jpg"],"use_cases":["It has an experimental payload, named Cascade, to demonstrate the world's first commercial space-based digital courier service based on store & forward file transfer techniques for message transfer. Cascade is designed to deliver data files of a size between 50 - 500 Gbyte at transmission speeds of 1.2 Gbyte/s over Ka-band when a ground station is within view of the spacecraft. "," The science mission, called ePOP (Enhanced Polar Outflow Probe), is comprised of eight instruments. The objective is to measure the interaction of the Earth's upper atmosphere with the solar wind (space weather monitoring with greatly improved prediction capability)"," Platform demonstration: The mission serves as a testbed for a Canadian spacecraft bus and subsystem functionality. The generic platform is considered for total mass budgets of 350-500 kg. The main goal of the program is to develop a minisatellite bus that may be used for future missions, including Earth observation, communications, space research and exploration."],"description":"Cascade, Smallsat and Ionospheric Polar Explorer (CASSIOPE),is a Canadian Space Agency (CSA) multi-mission satellite operated by the University of Calgary. The mission development and operations from launch to February 2018 was funded through CSA and the Technology Partnerships Canada program.In February, 2018 CASSIOPE became part of the European Space Agency's Swarm constellation through the Third Party Mission Program, known as Swarm Echo, or Swarm-E.It was launched September 29, 2013, on the first flight of the SpaceX Falcon 9 v1.1 launch vehicle.The satellite was deployed in an elliptical polar orbit and carries a commercial communications system called Cascade as well as a scientific experiment package called e-POP (enhanced Polar Outflow Probe)."},"39419":{"color":"#AA00FF","type":"Remote sensing","launch_date":"21 November 2013, 07:10:16 UTC","mission_duration":"5 years","launch_mass":"~300 kilograms (660 lb)","isGeoStationary":false,"tle_line1":"1 39419U 13066D   20265.55484810 -.00000150  00000-0 -89288-5 0  9990","tle_line2":"2 39419  97.5327 315.7859 0012388 242.1933 117.8041 14.94152915372527","name":"DUBAISAT-2","sat_name":"DUBAISAT-2","country_name":"UAE","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/UAE.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon5.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Dubaiaat-2_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Dubaiaat-2_2.jpg"],"use_cases":["DubaiSat-2 is propelled by a propulsion system called The Hall Effect Propulsion System. It is an electrical propulsion system with Xenon gas fuel and microwave cathode. The system will be used for the satellite's orbit correction and maintenance","It has an advanced Earth observation Camera which captures high resolution images and is capable of other functions such as high-speed data transmission"],"description":"DubaiSat-2 (Arabic: دبي سات-2‎) is an electro-optical Earth observation satellite built by the Emirates Institution for Advanced Science and Technology under an agreement with Satrec Initiative, a satellite manufacturing company in South Korea.EIAST's objective with DubaiSat-2 is to provide electro-optical images, that can be commercialized, for users within the United Arab Emirates and beyond and to develop and implement new technologies not used in DubaiSat-1. EIAST also intends to continue manpower training for the UAE's space program. 16 UAE engineers have been working on the design, development, testing and manufacturing of the satellite. The participation of the UAE engineers, who are currently working in South Korea, has increased by 100 per cent from the DubaiSat-1 project"},"39444":{"color":"#A07335","type":"Amateur Radio","launch_date":"21 November 2013","mission_duration":"6 years, 11 months and 27 days elapsed","launch_mass":"0.98 kilograms (2.2 lb)","isGeoStationary":false,"tle_line1":"1 39444U 13066AE  20261.51509835  .00000232  00000-0  34819-4 0  9993","tle_line2":"2 39444  97.5641 256.7176 0057144 329.1973  30.5892 14.82180331367001","name":"Educational single unit CubeSat satellite ","sat_name":"FUNCUBE-1 (AO-73)","country_name":"UK","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/UK.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon5.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/FUNcube-1_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/FUNcube-1_2.jpg"],"use_cases":["As part of its mission, FUNcube-1 carries a materials science experiment, from which the school students can receive telemetry data and which they can compare to the results they obtained from similar reference experiments in the classroom"],"description":"FUNcube-1 is a complete educational single unit CubeSat satellite with the goal of enthusing and educating young people about radio, space, physics and electronics. It is part of a program which aims to launch more of these educational CubeSats.It is the first satellite with outreach as its primary mission.FUNcube-1 was launched at 07:10:11 UTC on 21 November 2013, as part of the ISILaunch03 campaign, aboard a Dnepr rocket, from Yasny Launch Base, Russia. The launch was contracted by ISL, and operated by ISC Kosmotras."},"39509":{"color":"#A5BF9C","type":"Telecommunications","launch_date":"6 February 2014, 21:30:00 UTC","mission_duration":"15 years (planned)","launch_mass":"3080 kg","isGeoStationary":true,"tle_line1":"1 39509U 14006B   20259.86547302  .00000142  00000-0  00000-0 0  9997","tle_line2":"2 39509   0.0099 195.3659 0001154 357.1663 152.1125  1.00271504 24010","name":"Access on theatres for European allied forces nations-French Italian dual use satellite","sat_name":"ATHENA-FIDUS","country_name":"France-Italy  ","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/France-Italy.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon5.png ","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Athena-Fidus_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Athena-Fidus_2.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Athena-Fidus_3.jpg"],"use_cases":["Athena-Fidus will be used to provide broadband telecommunications services for police force terminals, interconnections between sites that have local telecommunications infrastructures, telecommunication services to provide remote surveillance in critical areas, and secure communications for armed forces.","Also, the satellite will be used to provide communication capabilities for emergency management in case of natural disasters or other catastrophic events. "],"description":"Athena-Fidus (Access on theatres for European allied forces nations-French Italian dual use satellite) is a French-Italian telecommunication satellite providing high-throughput secure communications to both nation's armed forces and their emergency services. It was manufactured by Thales Alenia Space under the supervision of CNES, the Direction générale de l'armement (DGA) and the Italian Space Agency (ASI). It complements the lower-throughput but more secure Syracuse 3 satellites. The satellite has a wet mass of 3080 kg and was placed on geostationary orbit in 2014. Its expected lifetime is 15 years.The satellite was launched by a Ariane 5 ECA rocket on 6 February 2014 during the VA217 mission.[3] After some on-orbit tests, official satellite commissioning took place on 14 March 2014."},"39766":{"color":"#336D49","type":"Remote sensing","launch_date":"24 May 2014, 03:05:14 UTC","mission_duration":"Elapsed: 6 years, 3 months, 25 days","launch_mass":"2,120 kilograms (4,670 lb)","isGeoStationary":false,"tle_line1":"1 39766U 14029A   20262.50631201  .00000090  00000-0  18992-4 0  9999","tle_line2":"2 39766  97.9226 357.7727 0001595  82.6110 277.5291 14.79471789341451","name":"Daichi 2","sat_name":"ALOS-2","country_name":"Japan","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Japan.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon3.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/ALOS-2_1.jpeg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/ALOS-2_2.jpg"],"use_cases":["The overall objective is to provide data continuity to be used for cartography, regional observation, disaster monitoring, and environmental monitoring."],"description":"Advanced Land Observing Satellite 2 (ALOS 2), also called Daichi 2, is a 2-ton Japanese satellite launched in 2014. Although the predecessor ALOS satellite had featured 2 optical cameras in addition to 1.2 GHz (L-band) radar, ALOS-2 had optical cameras removed to simplify construction and reduce costs. The PALSAR-2 radar is a significant upgrade of the PALSAR radar, allowing higher-resolution (1x3m per pixel) spotlight modes in addition to the 10m resolution survey mode inherited from the ALOS spacecraft. Also, the SPAISE2 automatic ship identification system and the Compact Infra Red Camera (CIRC) will provide supplementary data about sea-going ships and provide early warnings of missile launches.ALOS-2 was launched from Tanegashima, Japan, on 24 May 2014 by a H-IIA rocket."},"39768":{"color":"#BB00AA","type":"Technology demonstrator","launch_date":"24 May 2014; 6 years ago","mission_duration":"not defined","launch_mass":"48 kg (106 lb)","isGeoStationary":false,"tle_line1":"1 39768U 14029C   20268.29648111  .00000040  00000-0  11133-4 0  9996","tle_line2":"2 39768  97.8657   3.9335 0007675  28.3615 331.8007 14.84479462343315","name":"Space Optical Communications Research Advanced Technology Satellite","sat_name":"SOCRATES","country_name":"Russia","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Russia.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon2.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/SOCRATES_1.jpg"],"use_cases":["The goal is to experiment with QKD (Quantum Key Distribution) techniques and to foster the application free-space optical communications (higher data rates than RF communications) in microsatellites and eventually in nanosatellites.","The overall objective is to demonstrate and validate the operation of SOTA (Small Optical TrAnsponder), a laser communication system in space."],"description":"SOCRATES or Space Optical Communications Research Advanced Technology Satellite is a Japanese micro-satellite launched in 2014. The satellite is purely a technology demonstrator designed by NICT intended to help AES company to gain experience in basic mission control, attitude control and spacecraft communications. Its main experiment is SOTA (Small Optical TrAnsponder), an optical small satellite communications demonstrator.All subsystems of spacecraft are powered by solar cells mounted on spacecraft body and stub wings, with estimated electrical power of 120W BOL degrading to 100W EOL.SOCRATES was launched from Tanegashima, Japan, on 24 May 2014 at 03:05:00 UTC by an H-IIA 202."},"40016":{"color":"#CC1100","type":"Space Science","launch_date":"19 June 2014; 6 years ago","mission_duration":"undefined","launch_mass":"100 kg","isGeoStationary":false,"tle_line1":"1 40016U 14033G   20265.58714804 -.00000006  00000-0  63896-5 0  9998","tle_line2":"2 40016  97.6984 122.5347 0049273  37.0656 323.3940 14.75300993336931","name":"SaudiSat-4","sat_name":"SaudiSat-4","country_name":"UAE","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/KSA.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon3.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/SaudiSat-4_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/SaudiSat-4_2.jpg"],"use_cases":["The satellite is intended primarily for technology verification in space, main test piece being UV light diodes, intended for removal of static charge from the proof mass"," Proof mass experiment is expected to pave the way to engineering of gravitational reference sensor (GRS) of drag-free spacecraft","This devise has an applications for the tentative Laser Interferometer Space Antenna gravity wave detector"],"description":"SaudiSat-4 is a Saudi-Arabian micro-satellite launched in 2014. It was built by the National Satellite Technology Center at KACST.SaudiSat-4 was launched from Dombarovsky (air base) site 13, Russia, on 19 June 2014 by a Dnepr rocket."},"40267":{"color":"#9900ff","type":"Weather satellite","launch_date":"7 October 2014, 05:16 UTC","mission_duration":"8 years (planned)","launch_mass":"3500 kg","isGeoStationary":true,"tle_line1":"1 40267U 14060A   20271.44736878 -.00000298  00000-0  00000-0 0  9999","tle_line2":"2 40267   0.0174 179.5977 0001112 357.9810 130.8202  1.00274458 21916","name":"Himawari 8 (ひまわり8号)","sat_name":"Himawari 8","country_name":"Japan","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Japan.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon5.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Himawari_8_1.jpg"],"use_cases":["The role of Himawari 8 is to provide typhoon, rainstorm, weather forecast and other related reports for Japan, East Asia, and Western Pacific region","It is also responsible for ensuring the safety of ships, aviation and observing the environment of the earth."],"description":"Himawari 8 (ひまわり8号) is a Japanese weather satellite, the 8th of the Himawari geostationary weather satellites operated by the Japan Meteorological Agency. The spacecraft was constructed by Mitsubishi Electric with assistance from Boeing, and is the first of two similar satellites to be based on the DS-2000 satellite bus.Himawari 8 entered operational service on 7 July 2015 and is the successor to MTSAT-2 (Himawari 7) which was launched in 2006.Himawari 8 was launched atop a H-IIA rocket flying from the Yoshinobu Launch Complex Pad 1 at the Tanegashima Space Center. The launch occurred at 05:16 UTC on 7 October 2014 and reached its operational geostationary orbit in October 2014, at 35,786 kilometers and 140.7 degrees East."},"40733":{"color":"#00FFFF","type":"Communication","launch_date":"15 July 2015, 21:05 UTC","mission_duration":"15 years (planned)","launch_mass":"5,635 kilograms (12,423 lb)","isGeoStationary":true,"tle_line1":"1 40733U 15034B   20265.80192498 -.00000282  00000-0  00000-0 0  9999","tle_line2":"2 40733   0.0408  35.6843 0001231 157.3534  26.7981  1.00272249 19027","name":"STAR ONE C4","sat_name":"STAR ONE C4","country_name":"Brazil","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Brazil.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon3.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Star_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Star_2.jpg"],"use_cases":["Star One C4 uses the high-power variant of LS-1300, featuring two five-panel solar arrays that deliver 15.6kW of power to a dedicated system that conditions the satellite’s power bus and controls the state of charge of the vehicle’s batteries.","Star One C4 uses the high-power variant of LS-1300, featuring two five-panel solar arrays that deliver 15.6kW of power to a dedicated system that conditions the satellite’s power bus and controls the state of charge of the vehicle’s batteries."],"description":"Star One C4 is a Brazilian communications satellite. It was launched on 15 July 2015, 21:05 UTC by an Ariane 5ECA  carrier rocket, as part of a dual-payload launch with MSG-4. It was built by Space Systems/Loral, based on the SSL 1300E platform. It will be operated by Star One, a subsidiary of Embratel.The contract for the construction of Star One C4 was made in the year 2012, the company responsible was Space Systems/Loral, the date of completion of its construction and official launch occurred in July 2015. The launch date was confirmed by Arianespace on June 23, 2015. And the launching would take place on July 8, 2015. On July 3, the launcher announced the postponement of the launch for July 15, due Additional checks."},"41332":{"color":"#00FF33","type":" Earth observation Technology","launch_date":"7 February 2016, 00:30 UTC","mission_duration":"4 years (planned)","launch_mass":"150 to 200 Kilograms","isGeoStationary":false,"tle_line1":"1 41332U 16009A   20263.26083817  .00001218  00000-0  38456-4 0  9999","tle_line2":"2 41332  97.3152 333.7894 0023843  95.0446 265.3519 15.34539811258115","name":"Kwangmyongsong-4","sat_name":"KMS 4","country_name":"North Korea","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/North-Korea.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon5.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/KMS-4_1.jpg"],"use_cases":["It observes earth and collects data","It also serves as a communication satellite"],"description":"Kwangmyongsong-4 (Korean for 'Bright Star-4 or Lodestar-4') or KMS-4  is a reconnaissance satellite launched by North Korea on 7 February 2016. The launch happened after North Korea conducted a nuclear test on 6 January and as the United Nations Security Council was deciding on sanctions to be placed on the country following the nuclear test. The launch was also timed to celebrate the 74th birthday of the late leader Kim Jong-il on February 16."},"41877":{"color":"#FF00FF","type":"Remote-sensing","launch_date":"Dec 7, 2016","mission_duration":"5 years","launch_mass":"1,235 kilograms (2,723 lb)","isGeoStationary":false,"tle_line1":"1 41877U 16074A   20266.43190928 -.00000009  00000-0  16086-4 0  9992","tle_line2":"2 41877  98.7888 336.0312 0001331  81.0451 279.0877 14.21594061196821","name":"RESOURCESAT-2A","sat_name":"RESOURCESAT-2A","country_name":"INDIA","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/India.jpg","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon5.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/ResourceSat-1_1.jpeg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/ResourceSat-1_2.jpeg"],"use_cases":["Resourcesat-2A provides continuity of data with higher temporal resolution (therefore more cloud-free data) for various operational applications, namely, crop production estimation, forest cover mapping, mapping and monitoring land & water resources, environmental applications, rural & urban development plans, geological applications, disaster management support etc","The typical advantage of improved revisit as a consequence of Resourcesat-2 & 2A constellation is extremely useful for monitoring vegetation and water resources."],"description":"Resourcesat-2A is a follow on mission to Resourcesat-1 and Resourcesat-2 which were launched in October, 2003 and April, 2011 respectively. The new satellite provides the same services as the other Resourcesat missions. It will give regular micro and macro information on land and water bodies below, farm lands and crop extent, forests, mineral deposits, coastal information, rural and urban spreads besides helping in disaster management.The satellite was launched aboard PSLV-C36,the 38th flight of PSLV, on December 7, 2016. For the first time, Navic processor and receiver was used to navigate PSLV."},"41898":{"color":"#22F33F","type":"Earth observation","launch_date":"21 December 2016, 19:22 UTC[2]","mission_duration":"Planned: 3 years Elapsed: 3 years, 8 months, 27 days","launch_mass":"620 kg (1,370 lb","isGeoStationary":false,"tle_line1":"1 41898U 16081A   20262.54396613  .00000090  00000-0  30546-4 0  9996","tle_line2":"2 41898  98.2883 203.6232 0020730 312.6310  47.3124 14.56769472198961","name":"CarbonSat","sat_name":"TanSat","country_name":"China","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/China.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon5.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/TanSat_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/TanSat_2.jpg"],"use_cases":["TanSat uses the ground segment established for the operation of the Fengyun weather satellites including the Mission Control tools, science data reception and distribution functions and archiving protocols in an overall effort to reduce cost by implementing a high degree of commonality between different missions.","Monitores CO2 in earth's atmosphere"],"description":"TanSat, also known as CarbonSat, is a Chinese Earth observation satellite dedicated to monitoring carbon dioxide in Earth's atmosphere. It is generally classified as a minisatellite, and is the first dedicated carbon mission of the Chinese space program.The mission was formally proposed in 2010, and work began in January 2011.It is funded by the Ministry of Science and Technology (MOST) and was built by the Shanghai Institute of Microsystem And Information Technology (SIMIT).TanSat carries two instruments: the Carbon Dioxide Spectrometer and the Cloud and Aerosol Polarimetry Imager. The Carbon Dioxide Spectrometer (CDS), also called CarbonSpec, is a high-resolution grating spectrometer which measures CO2 absorption at 1.61 μm and 2.06 μm, and O2 absorption in reflected sunlight at 0.76 μm. The Cloud and Aerosol Polarimetry Imager (CAPI) is a wide-field, moderate-resolution, imaging spectrometer which works in concert with CDS by compensating for measurement errors caused by clouds and aerosols. It makes observations in ultraviolet (0.38 μm), visible (0.67 μm), and near infrared (0.87 μm, 1.375 μm, and 1.64 μm)"},"43132":{"color":"#661100","type":"Astronomy · Planetary science","launch_date":"January 12, 2018, 03:58 UTC","mission_duration":"~1 year","launch_mass":"3.9 kg","isGeoStationary":false,"tle_line1":"1 43132U 18004X   20271.44449258  .00002102  00000-0  87924-4 0  9997","tle_line2":"2 43132  97.4392 342.1300 0010893  60.7455 299.4871 15.24651480150596","name":"PICSAT","sat_name":"PICSAT","country_name":"France","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/France.jpg","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon3.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/PicSat_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/PicSat_2.jpg"],"use_cases":["Its main task is to measure the transit of the planet Beta Pictoris b in front of its star Beta Pictoris."],"description":"PicSat is a French nano-satellite, a CubeSat made of 3 units (3U), designed to measure the transit of the planet Beta Pictoris b in front of its star Beta Pictoris. PicSat was designed and built by a small team of scientists and engineers led by Dr. Sylvestre Lacour, astrophysicist and instrumentalist at the High Angular Resolution in Astrophysics group of the LESIA laboratory at the Paris Observatory / PSL Research University / CNRS. The satellite was launched on January 12, 2018. It operated for more than 10 weeks, then fell silent on March 20, 2018"},"43463":{"color":"#008000","mission_duration":"15 years","launch_mass":"~3,709 kg (8,177 lb)","type":"Communications and Broadcasting Satellite","launch_date":"12 May 2018, 20:14 UTC","isGeoStationary":true,"tle_line1":"1 43463U 18044A   20257.44108278 -.00000370  00000-0  00000+0 0  9997","tle_line2":"2 43463   0.0096 213.6964 0001933 333.4638  83.6251  1.00271768  8649","name":"Bangabandhu Satellite-1","sat_name":"BANGABANDHUSAT-1","country_name":"Bangladesh","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/bangladesh.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon5.png","description":"The Bangabandhu Satellite-1 (Bangabandhu-1) is the first Bangladeshi geostationary communications and broadcasting satellite. It is named after the father of the nation, Bangabandhu Sheikh Mujibur Rahman. It was manufactured by Thales Alenia Space and launched on 12 May 2018.The satellite was the first payload launched by a SpaceX Falcon 9 Block 5 rocket.Its life span is predicted 15 years.Its launch mass eas 3709 kg.The total cost of the satellite was projected to be 248 million US dollars in 2015 (Tk 19.51 billion), financed via a $188.7 million loan from HSBC Holdings plc.It is moving at a velocity of 3.07 km/s","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/bangabandhuSatellite.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/bangabandhuSatellite2jpg.jpg"],"use_cases":["Bangabandhu 1 provides broadcasting and telecommunication services to rural areas in Bangladesh. It also supports profitable services, including direct-to-home (DTH) services.","Bangabandhu 1 offers Ku-band and C-band services across Bangladesh and its territorial waters of the Bay of Bengal, India, Nepal, Bhutan, Sri Lanka, the Philippines, and Indonesia.","Bangabandhu 1 enables the nation to save approximately BDT1.08bn ($14m) spent on satellite rents a year. BTRC also plans to launch follow-on series of BD-2 and BD-3 satellites in phases."]},"43530":{"color":"#FFDD00","type":"Oceanography and Meteorology Earth observation satellite GIS and GPS","launch_date":"9 July 2018, 03:56:10 UTC","mission_duration":"5 years","launch_mass":"3500 kilograms ","isGeoStationary":false,"tle_line1":"1 43530U 18056B   20266.44680048  .00000003  00000-0  77422-5 0  9995","tle_line2":"2 43530  97.9918 342.4570 0001402  82.4006 277.7364 14.76911706119012","name":"The Pakistan Remote Sensing Satellite","sat_name":"PRSS 1","country_name":"Pakistan","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Pakistan.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon5.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/PRSS_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/PRSS_2.jpg"],"use_cases":["According to SUPARCO, first satellite of this program was launched in 2018, and it is visioned to provide help in exploiting the potentials of space technologies for natural resource surveying and environmental purpose."],"description":"The Pakistan Remote Sensing Satellite (PRSS), commercially known as Remote Sensing Satellite System (RSSS), is a dual-purpose Earth observational and optical satellite. Pakistan Remote Sensing Satellite-1 (PRSS-1) was launched from China's Jiuquan Satellite Centre on 9 July 2018.On 9 July 2018 at 03:56:10 UTC, China successfully launched two remote sensing satellites for Pakistan, which were launched to monitor progress as they build the China–Pakistan Economic Corridor, an extensive multibillion-dollar infrastructure development project between the two nations. The satellites were named PRSS-1 and PakTES-1A, and were launched from the Jiuquan Satellite Launch Center in northwest China using a Long March 2C launch vehicle. The PRSS-1 is China's first optical remote sensing satellite sold to Pakistan. It is the 17th satellite developed by the China Academy of Space Technology for an overseas buyer, while PakTES-1A is an experimental satellite of Space & Upper Atmosphere Research Commission (SUPARCO)"},"43591":{"color":"#00dd00","type":"Technology demonstration","launch_date":"29 June 2018, 09:42 UTC","mission_duration":"6-9 months (planned)","launch_mass":"1.11 kg","isGeoStationary":false,"tle_line1":"1 43591U 98067PF  20271.07558959  .00134404  00000-0  48449-3 0  9990","tle_line2":"2 43591  51.6322 118.4285 0004187 185.5458 174.5502 15.87749123121991","name":"BHUTAN-1","sat_name":"BHUTAN-1","country_name":"Bhutan","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Bhutan.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon2.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Bhutan-1_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/BHUTAN-1_2.jpg"],"use_cases":["Take photograph of homeland via 2 onboard cameras of 5 MP.","Receive and send text messages message from an amateur radio","Broadcast/relay to all other amateurs within the range"],"description":"BHUTAN-1 was the first Bhutanese nanosatellite to be launched into space. The satellite was built during Kyushu Institute of Technology's Birds-2 program. The Birds program helps countries fly their first satellite. BHUTAN-1 was launched into orbit aboard the SpaceX CRS-15 mission on 29 June 2018. It was deployed from the Kibō module of the International Space Station (ISS) on 10 August 2018. The satellite has cameras to image the Earth.BHUTAN-1 was launched to space on 29 June 2018, via the Falcon 9 Full Thrust rocket at Cape Canaveral in Florida, as part of the SpaceX CRS-15 Commercial Resupply Service mission. Maya-1 and UiTMSAT-1 which were also developed under the Birds-2 project was also among the payload of the rocket. BHUTAN-1 was deployed from Kibō module of the International Space Station (ISS) in August 2018, becoming the first Bhutanese satellite"},"43719":{"color":"#A4AD19","type":"Earth observation satellite","launch_date":"04:27:30 UTC 29 November 2018","mission_duration":"Planned: 5 years Elapsed: 1 year, 9 months, 17 days","launch_mass":"380 kilograms (840 lb)","isGeoStationary":false,"tle_line1":"1 43719U 18096A   20260.49027055  .00000049  00000-0  13703-4 0  9991","tle_line2":"2 43719  97.8965 326.0566 0009271 297.1795  62.8479 14.78766645 97126","name":"Hyperspectral Imaging Satellite","sat_name":"HYSIS","country_name":"India ","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/India.jpg","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon2.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/HySIS_1.jpeg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/HySIS_1.jpeg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/HySIS_3.jpeg"],"use_cases":["It will provide hyperspectral imaging services to India for a range of applications in agriculture, forestry and in the assessment of geography such as coastal zones and inland waterways "],"description":"HySIS (Hyperspectral Imaging Satellite) is an Earth observation satellite which will provide hyperspectral imaging services to India for a range of applications in agriculture, forestry and in the assessment of geography such as coastal zones and inland waterways[2][3] The data will also be accessible to India's defence forces.PSLV-C43 carrying HySIS and 30 secondary payloads was launched at 04:27:30 UTC, 29 November 2018 from First Launch Pad of Satish Dhawan Space Centre. After a flight that lasted 17 minutes and 19 seconds, HySIS was successfully placed in a planned sun-synchronous polar orbit at around 645 km"},"43807":{"color":"#229960","type":"Life sciences research","launch_date":"3 December 2018","mission_duration":"Planned: 1 year","launch_mass":"250 kg (550 lb","isGeoStationary":false,"tle_line1":"1 43807U 18099BB  20267.29236892  .00000454  00000-0  45963-4 0  9991","tle_line2":"2 43807  97.6989 337.5097 0011989 214.1144 145.9307 14.95654970 97972","name":"Euglena and Combined Regenerative Organic-Food Production in Space","sat_name":"EUCROPIS","country_name":"Germany","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Germany.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon5.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Eucropis_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Eucropis_2.jpg"],"use_cases":["The aim is to develop a stable, and symbiotic biological life support system while being exposed to gravity levels similar to those on the lunar surface as well as the surface of Mars","Eu:CROPIS is intended to examine the possibility of using not only water, but also previously disposed waste to grow fruits and vegetables after proper conversion.","Two life support systems (a nitrifying trickle filter system and the single-celled algae Euglena gracilis) within the satellite will be used for producing biomass out of artificial urine in a closed system. Furthermore, the algae Euglena gracilis will protect the biosystem against high levels of ammonia present in urine"],"description":"Eu:CROPIS (Euglena and Combined Regenerative Organic-Food Production in Space) is a life science satellite developed by the German Aerospace Center (DLR) and is intended to investigate the possibility of growing plants (specifically tomatoes) in different levels of gravity, such as on the Moon and Mars,as a sustainable food source while using human urine for moisture and as the source of fixed nitrogen.This orbital mission will simulate and test two greenhouses that could be scaled up and assembled inside a lunar or Martian habitat to provide the crew with a local source of fresh food, while recycling human urine into fertiliser. Some microorganisms will be added to convert synthetic urine into easily digestible fertilisers for the tomatoes. The aim is to develop a stable, closed-loop, bio-regenerative life support system functioning in low gravity.In more detail, porous lava stones will be fitted in trickle filters and dried soil containing normal soil microbial colonies. Microbes then use nitrite (NO−\n2) to convert the harmful ammonia (NH\n3) into nitrate (NO−\n3), which is then added to six tomato seeds as liquid fertiliser.In addition, the system incorporates a colony of the single-cell microorganism Euglena gracilis, which is a photosynthetic algae able to produce oxygen and biomass while protecting the whole system against high ammonia concentrations. This oxygen is necessary for the conversion of urine to nitrate until the photosynthetic oxygen production by the tomatoes is sufficient"},"44048":{"color":"#00DDFF","type":"Communication","launch_date":"22 February 2019, 01:45 UTC","mission_duration":"15 years (planned)","launch_mass":"4100 kg","isGeoStationary":true,"tle_line1":"1 44048U 19009A   20270.55716983 -.00000258  00000-0  00000-0 0  9990","tle_line2":"2 44048   0.0172 184.6265 0002047 346.6905 181.0820  1.00270132  5809","name":"NUSANTARA SATU","country_name":"Indonesia","sat_name":"PSN VI or PSN-6","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Indonesia.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon4.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Nusantara_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Nasuntara_2.jpg"],"use_cases":["The satellite offers a total bandwidth of 15 gigabits per second","Its expected service time is a minimum of 15 years. It will provide communications links to rural parts of Indonesia, allowing PSN to expand broadband internet services into these regions"],"description":"Nusantara Satu or Nusantara Satu 1 (formerly known as PSN VI or PSN-6) is an Indonesian communications satellite. It is a large high-throughput satellite (HTS) providing voice and data communications, and broadband Internet throughout the Malay archipelago and South East Asia. Nusantara Satu was built by SSL and was launched in February 2019 on a Falcon 9 Block 5 rocket along with the satellite Beresheet (2019-009B) Moon lander by SpaceIL and Israel Aerospace Industries (IAI) and the microsat S5 (2019-009D) by the USAFRL.Nusantara Satu is a communications satellite developed and designed by SSL for PT Pasifik Satelit Nusantara (PSN), the first private company in the telecommunications and information services sector in Indonesia.The project's cost is US$230 million.The massive satellite, features solar-electric ion thrusters,[2] but it also employs conventional chemical propellant for stationkeeping in orbit"},"44072":{"color":"#00AAFF","type":"Earth observation Technology","launch_date":"22 March 2019, 01:50:35 UTC","mission_duration":"5 years (planned)","launch_mass":"830 kilograms (1,830 lb)","isGeoStationary":false,"tle_line1":"1 44072U 19015A   20264.51923984 -.00000053  00000-0  00000+0 0  9996","tle_line2":"2 44072  97.8539 337.9615 0001550  81.7868 278.3523 14.83674358 81315","name":"PRecursore IperSpettrale della Missione Applicativa, Hyperspectral PRecursor of the Application Mission","sat_name":"PRISMA","country_name":"Italy","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Italy.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon3.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/PRISMA_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/PRISMA_2.jpg"],"use_cases":["Implementation of an Earth Observation pre-operative mission","In orbit demonstration and qualification of an Italian state-of-the-art hyperspectral/panchromatic technology.","Validation of end-to-end data processing able to develop new applications of spectral resolution images."],"description":"PRISMA (PRecursore IperSpettrale della Missione Applicativa, Hyperspectral PRecursor of the Application Mission) is an Italian Space Agency pre-operational and technology demonstrator mission focused on the development and delivery of hyperspectral products and the qualification of the hyperspectral payload in space.PRISMA is an Earth observation satellite with innovative electro-optical instrumentation which combines a hyperspectral sensor with a medium-resolution panchromatic camera. The advantages of this combination are that in addition to the classical capability of observation based on the recognition of the geometrical characteristics of the scene, there is the one offered by hyperspectral sensors which can determine the chemical-physical composition of objects present on the scene. This offers the scientific community and users many applications in the field of environmental monitoring, resource management, crop classification, pollution control, etc. Further applications are possible even in the field of National Security.PRISMA is a program completely funded by ASI and is a follow on of HypSEO mission.The satellite was launched on 22 March 2019 aboard a Vega rocket."},"45026":{"color":"#FF00FF","type":"Communication","launch_date":"16 January 2020, 21:05 (UTC)","mission_duration":"Planned: 15 years Elapsed: 7 months, 30 days","launch_mass":"3,357 kg (7,401 lb)","isGeoStationary":true,"tle_line1":"1 45026U 20005A   20258.43888708 -.00000174  00000-0  00000+0 0  9999","tle_line2":"2 45026   0.0191 152.0658 0003534 304.1600 138.6469  1.00269284  2456","name":"GeoStationary Satellite ","sat_name":"GSAT-30","country_name":"INDIA","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/India.jpg","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon4.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/GSAT-30_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/GSAT-30_2.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/GSAT-30_3.jpg"],"use_cases":["GSAT-30 provides DTH Television Services, connectivity to VSATs for ATM, stock-exchange, television uplinking and teleport services, Digital Satellite News Gathering (DSNG) and e-governance applications. The satellite will also be used for bulk data transfer for a host of emerging.","The satellite will also be used for bulk data transfer for a host of emerging telecommunication applications.","The satellite will provide communication services to Indian mainland and islands through Ku-band and wide coverage covering Gulf countries, a large number of Asian countries and Australia through C-band."],"description":"GSAT-30 is a telecommunication satellite developed by the Indian Space Research Organisation (ISRO).GSAT-30 satellite was launched aboard Ariane-5 launch vehicle (VA251) from French Guiana on 21:05 UTC ,16 January 2020 or 02:35 IST, 17 January 2020.After three orbit raising burns with cumulative duration of 2 hours 29 minutes,GSAT-30 acquired station at 81°E on 25 January 2020.Its initial mass was 3357 Kg during launch."},"45937":{"color":"#11DDAA","type":"ISS resupply","launch_date":"23 July 2020 14:26:21 UTC","mission_duration":"63 days, 6 hours, 31 minutes","launch_mass":"7000 kg","isGeoStationary":false,"tle_line1":"1 45937U 20050A   20268.38302949 -.00000284  00000-0  29810-5 0  9991","tle_line2":"2 45937  51.6429 217.6155 0001685  99.1844  43.7718 15.48778094247397","name":"Progress MS-15 (Russian: Прогресс МC-15)","sat_name":"PROGRESS-MS 15","country_name":"Russia","country_flag":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/countryFlag/Russia.png","icon_url":"https://satellitedatanasa.s3.ap-south-1.amazonaws.com/satIcon/satIcon3.png","real_images":["https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Progress_1.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Progress_2.jpg","https://satellitedatanasa.s3.ap-south-1.amazonaws.com/realImages/Progress_3.jpg"],"use_cases":["Its main task is to resupply the  ISS"],"description":"Progress MS-15 (Russian: Прогресс МC-15), identified by NASA as Progress 76, is a Progress spacecraft launched by Roscosmos to resupply the International Space Station (ISS).A Soyuz-2.1a launch vehicle was used to launch Progress MS-15 to the International Space Station.Progress MS-15 was launched at 14:26:21 UTC from Baikonur Site 31/6 on a fast-track trajectory. Following a nominal launch, Progress MS-15 docked with the Pirs port on the ISS two orbits later at 17:45:00 UTC"}}
